.nut-button{cursor:pointer;height:40px;padding:0 40px;outline:none;font-size:14px;color:#fff;background:-o-linear-gradient(135deg, #ff4f18 0%, #f20000 100%);background:linear-gradient(315deg, #ff4f18 0%, #f20000 100%);border:none;-webkit-box-sizing:border-box;box-sizing:border-box}.nut-button[disabled]{cursor:not-allowed;background:#dadada;color:#f6f6f6}.nut-button .txt-icon{vertical-align:text-top;width:16px;height:16px;margin-right:5px;color:#f0250f}.nut-button.red{background:#fff;border:1px solid #f0250f;color:#f0250f}.nut-button.gray{background:#f6f6f6;color:#2d2d2d}.nut-button.light{color:#2d2d2d;background:#fff;border:1px solid #dadada}.nut-button.lightred{background:rgba(240,37,15,0.05);color:#2d2d2d;border:none}.nut-button.primary{background:#f0250f;color:#fff}.nut-button.default{background:#fff;color:#2d2d2d;border:1px solid #ababab}.nut-button.actived{background:rgba(240,37,15,0.05);color:#f0250f;border:1px solid #f0250f}.nut-button.dashed{background:#fff;color:#2d2d2d;border:1px dashed #dadada}.nut-button.block{width:100%;-webkit-border-radius:0;border-radius:0}.nut-button.small{height:30px;padding:0 15px;font-size:12px}.nut-button.no-txt-small{width:30px;padding:0}.nut-button.no-txt-small .txt-icon{margin:0}.nut-button.no-txt{width:40px;padding:0}.nut-button.no-txt .txt-icon{margin:0}.nut-button.circle{-webkit-border-radius:50px;border-radius:50px}
