.nut-luckdraw{position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);-ms-transform:translate(-50%, -50%);transform:translate(-50%, -50%);text-align:center}.nut-luckdraw .drawTable-name{position:absolute;left:10px;top:20px;width:-webkit-calc(100% - 20px);width:calc(100% - 20px);font-size:12px;text-align:center;color:#ff5722}.nut-luckdraw .drawTable-img{position:absolute;left:-webkit-calc(50% - 30px / 2);left:calc(50% - 30px / 2);top:60px;width:30px;height:30px}.nut-luckdraw .drawTable-img img{display:inline-block;width:100%;height:100%}.nut-luckdraw .lucktable{position:absolute;left:0;top:0;width:100%;height:100%}.nut-luckdraw .prize{position:absolute;left:25%;top:0;width:50%;height:50%}.nut-luckdraw .prize .item{position:absolute;left:0;top:0;width:100%;height:100%;-webkit-transform-origin:center bottom;-ms-transform-origin:center bottom;transform-origin:center bottom}.pointer{position:absolute;left:50%;top:50%;-webkit-transform:translate(-43.75%, -50%);-ms-transform:translate(-43.75%, -50%);transform:translate(-43.75%, -50%)}
