.nut-leftslip .slipbtns a{display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;width:40px;background:#ff4949;color:#fff;height:100%;padding:0 5px;font-size:14px;-webkit-box-align:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center;text-align:center}
