agination{right:10px;top:50%;transform:translate3d(0, -50%, 0);-webkit-transform:translate3d(0, -50%, 0)}.nut-swiper.vertical .nut-swiper-pagination .swiper-pagination-bullet{display:block;margin:6px 0}.nut-swiper.horizontal .nut-swiper-pagination{bottom:10px;width:100%;text-align:center}.nut-swiper.horizontal .nut-swiper-pagination .swiper-pagination-bullet{display:inline-block;margin:0 3px}.nut-swiper img{pointer-events:none}
