.nut-imagepicker{display:block}.nut-imagepicker .img-list{display:inline-block}.nut-imagepicker .img-list .img-item{display:inline-block;margin:0 0 5px 0}.nut-imagepicker .img-list .img-item a{display:inline-block;width:100%;height:100%}.nut-imagepicker .img-list .img-item a img{width:100%;height:100%;-webkit-border-radius:6px;border-radius:6px;-webkit-transition:all 5s ease-in-out;-o-transition:all 5s ease-in-out;transition:all 5s ease-in-out}.nut-imagepicker .img-list .img-item:last-child{margin-right:0}.nut-imagepicker .img-list .add-icon{display:inline-block;position:relative;border:1px dashed #f0250f;-webkit-border-radius:6px;border-radius:6px;-webkit-box-sizing:border-box;box-sizing:border-box;cursor:pointer}.nut-imagepicker .img-list .add-icon i{position:absolute;top:0;left:0;right:0;bottom:0;margin:auto;width:20px;height:20px;color:#f0250f}.nut-imagepicker .img-list .add-icon i svg{vertical-align:top;fill:currentColor}.nut-imagepicker .img-list .add-icon input[type='file']{position:absolute;width:100%;height:100%;opacity:0;top:0;left:0}
