.nut-avatar{background-size:100% 100%;background-repeat:no-repeat;background-position:center center;display:inline-block;position:relative;margin-right:10px}.nut-avatar .icon{width:50%;height:50%;background-size:100% 100%;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);-ms-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.nut-avatar .text{display:inline-block;width:100%;height:100%;text-align:center;overflow:hidden}.avatar-large{width:40px;height:40px;line-height:40px}.avatar-small{width:24px;height:24px;line-height:24px}.avatar-normal{width:32px;height:32px;line-height:32px}.avatar-round{-webkit-border-radius:50%;border-radius:50%}.avatar-square{-webkit-border-radius:5px;border-radius:5px}
